<script>
import { TweenMax } from 'gsap';

export default {
  name: 'QuestionChoice',
  props: {
    state: {
      type: String,
      // Options: default, correct, wrong, unselected-correct, unselected-wrong
      default: 'default',
    },
  },
  computed: {
    imageFullPath() {
      return `/images/illustrations/${this.imagePath}`;
    },
  },
  methods: {
    animateOnSelection() {
      // if (this.state === 'unselected-correct' || this.state === 'unselected-wrong') {
      if (this.state === 'wrong' || this.state === 'unselected-wrong') {
        const correctBg = this.$refs.backgroundCorrect;
        TweenMax.to(correctBg, 0.4, {
          opacity: 0,
        });

        const wrongBg = this.$refs.backgroundWrong;
        TweenMax.to(wrongBg, 0.4, {
          opacity: 1,
        });
      }
    },
  },
};
</script>

