<template>
  <div class="app">
    <div class="container">
      <transition
        name="page-change"
        v-on:after-leave="updateHeight"
      >
        <router-view
          @heightChanged="updateHeight"
          @openPopup="openPopup"
        />
      </transition>
    </div>
    <transition name="fade-popup-dimmer">
      <Popup
        ref="popup"
        v-show="isShowingPopup"
        :title="popupTitle"
        :body="popupBody"
        :isVisible="isShowingPopup"
        @closePopup="closePopup"
      />
    </transition>
  </div>
</template>

<script>
import pym from 'pym.js';
import Popup from '@/components/Popup.vue';

let pymChild = null;

export default {
  name: 'App',
  components: { Popup },
  data() {
    return {
      isShowingPopup: false,
      popupTitle: '',
      popupBody: '',
    };
  },
  mounted() {
    pymChild = new pym.Child({
      renderCallback: () => {
        if (pymChild) {
          pymChild.sendHeight();
        }
      },
    });
    this.updateHeight();
  },
  methods: {
    updateHeight() {
      if (pymChild) {
        pymChild.sendHeight();
      }
    },
    openPopup(title = '', body = '') {
      this.popupTitle = title;
      this.popupBody = body;
      this.isShowingPopup = true;
      this.$refs.popup.center();
    },
    closePopup() {
      this.isShowingPopup = false;
    },
  },
};
</script>

<style lang="scss">
@import "./css/main.scss";
</style>
