import Vue from 'vue';
import Router from 'vue-router';

import Q01 from './views/Q01.vue';
import Q02 from './views/Q02.vue';
import Q03 from './views/Q03.vue';
import Q04 from './views/Q04.vue';
import Q05 from './views/Q05.vue';
import ResultPage from './components/ResultPage.vue';

Vue.use(Router);

const FIRST_QUESTION_NAME = 'question1';
const RESULT_PAGE_NAME = 'result';

const router = new Router({
  routes: [
    {
      path: '/',
      name: FIRST_QUESTION_NAME,
      component: Q01,
    },
    {
      path: '/question2',
      name: 'question2',
      component: Q02,
    },
    {
      path: '/question3',
      name: 'question3',
      component: Q03,
    },
    {
      path: '/question4',
      name: 'question4',
      component: Q04,
    },
    {
      path: '/question5',
      name: 'question5',
      component: Q05,
    },
    {
      path: '/result',
      name: RESULT_PAGE_NAME,
      component: ResultPage,
    },
  ],
});

function inIframe() {
  try {
    return window.self !== window.top;
  } catch (e) {
    return true;
  }
}

router.beforeEach((to, from, next) => {
  try {
    if (inIframe()) {
      // Scroll up to the question paragraph, or the correct score big number
      // except the initial loading
      if (to.name !== FIRST_QUESTION_NAME || from.name === RESULT_PAGE_NAME) {
        // For result page, scroll back to the top of iframe
        let offset = 0;

        // For question pages 2-5, scroll back to the question text
        if (to.name !== RESULT_PAGE_NAME && to.name !== FIRST_QUESTION_NAME) {
          const questionBox = document.querySelector('.question-box');
          if (questionBox) {
            const innerBodyRect = document.body.getBoundingClientRect();
            const questionBoxRect = questionBox.getBoundingClientRect();
            offset = questionBoxRect.top - innerBodyRect.top;
          }
        }

        window.parent.postMessage({
          type: 'scroll-to-position',
          offset,
        }, '*');
      }
    } else {
      // console.log('The Quiz is not inside of iframe. Skipped scrolling');
    }
  } catch (error) {
    // console.error('Error during scrolling', error);
  }
  next();
});

export default router;
