<template>
  <div class="popup">
    <div class="dimmer">
    </div>
    <div class="window-container" ref="windowContainer" :style="{top: containerTop + 'px'}">
      <transition name="fade-popup-window">
        <article v-show="isVisible" class="popup-window">
          <h2 class="header purple-fg">{{title}}</h2>
          <p class="body">{{body}}</p>
          <button @click="$emit('closePopup')">Close</button>
        </article>
      </transition>
    </div>
  </div>
</template>

<script>
import getVerticalPositioningInfo from '../util/getVerticalPositioningInfo';

const POPUP_VERTICAL_OFFSET = 40;

export default {
  name: 'Popup',
  data() {
    return {
      containerTop: 0,
    };
  },
  props: {
    title: String,
    body: String,
    isVisible: Boolean,
  },
  methods: {
    center() {
      getVerticalPositioningInfo()
        .then((info) => {
          const popupHeight = this.$refs.windowContainer.clientHeight;
          const parentBrowserHeight = info.parentInnerHeight;
          const iframeTop = info.iframeBoundingRect.top;
          const iframeHeight = info.iframeBoundingRect.height;

          // Calculate the offset
          const idealOffset = (parentBrowserHeight - popupHeight - iframeTop * 2) / 2;
          const minimumPossibleOffset = POPUP_VERTICAL_OFFSET;
          const maximumPossibleOffset = iframeHeight - POPUP_VERTICAL_OFFSET - popupHeight;

          // Make sure the bottom part is not cut-off
          let finalOffset = Math.min(idealOffset, maximumPossibleOffset);

          // Make sure the top part is not cut-off.
          finalOffset = Math.max(finalOffset, minimumPossibleOffset);

          // Set the final offset.
          // When the window height is smaller than popup height,
          // it still centers popup, even though both top and bottom is cut-off
          this.containerTop = finalOffset;
        });
    },
  },
};
</script>

