<template>
  <div class="question-choice-list">
    <transition name="fade-fast">
      <div ref="firstChoiceClipping" class="first-choice-clipping">
        <div
          v-show="showsFirstOption"
          class="card-container"
          :class="stateForFirstOption"
        >
          <QuestionChoice
            :state="stateForFirstOption"
            @click="firstChoiceClicked"
            ref="firstChoice"
          >
            <template v-slot:choice-image>
              <slot name="choice1-image"></slot>
            </template>
            <template v-slot:choice-text>
              <slot name="choice1-text"></slot>
            </template>
          </QuestionChoice>
        </div>
      </div>
    </transition>
    <transition name="fade-fast">
      <div
        v-show="showsSecondOption"
        class="card-container"
        :class="stateForSecondOption"
      >
        <QuestionChoice
          :state="stateForSecondOption"
          @click="secondChoiceClicked"
          ref="secondChoice"
        >
          <template v-slot:choice-image>
            <slot name="choice2-image"></slot>
          </template>
          <template v-slot:choice-text>
            <slot name="choice2-text"></slot>
          </template>
        </QuestionChoice>
      </div>
    </transition>
  </div>
</template>

<script>
import { TweenMax } from 'gsap';
import QuestionChoice from '@/components/QuestionChoice.vue';

export default {
  name: 'QuestionChoiceList',
  components: {
    QuestionChoice,
  },
  props: {
    correctChoiceIndex: Number,
  },
  data() {
    return {
      didUserSelectOption: false,
      userSelectedIndex: -1,
      showsFirstOption: true,
      showsSecondOption: true,
    };
  },
  computed: {
    anyChoiceIsValid() {
      return this.correctChoiceIndex === Infinity;
    },
    choiceIsCorrect() {
      return this.correctChoiceIndex === this.userSelectedIndex;
    },
    stateForFirstOption() {
      if (this.didUserSelectOption) {
        // if both answers are right, mark both as correct
        if (this.anyChoiceIsValid) {
          return 'correct';
        }

        if (this.userSelectedIndex === 0) {
          return this.choiceIsCorrect ? 'correct' : 'wrong';
        }
        return this.choiceIsCorrect ? 'unselected-wrong' : 'unselected-correct';
      }
      return 'default';
    },
    stateForSecondOption() {
      if (this.didUserSelectOption) {
        // if both answers count as correct, mark both as correct
        if (this.anyChoiceIsValid) {
          return 'correct';
        }

        if (this.userSelectedIndex === 1) {
          return this.choiceIsCorrect ? 'correct' : 'wrong';
        }
        return this.choiceIsCorrect ? 'unselected-wrong' : 'unselected-correct';
      }
      return 'default';
    },
  },
  methods: {
    firstChoiceClicked() {
      this.userDidSelectIndex(0);
    },
    secondChoiceClicked() {
      this.userDidSelectIndex(1);
    },
    userDidSelectIndex(index) {
      if (this.didUserSelectOption === false) {
        if (this.anyChoiceIsValid || this.correctChoiceIndex === index) {
          this.$store.commit('userDidSelectCorrectAnswer');
        }
        this.userSelectedIndex = index;
        this.didUserSelectOption = true;

        this.$nextTick(() => {
          this.$emit('useSelectedChoice');
          const { firstChoice, secondChoice } = this.$refs;

          const isMobileDevice = window.matchMedia('screen and (max-width: 599px)').matches;
          if (isMobileDevice) {
            // Mobile
            if (this.userSelectedIndex === 0) {
              // First option is selected
              firstChoice.animateOnSelection();
              this.showsSecondOption = false;
            } else {
              // Second option is selected
              secondChoice.animateOnSelection();
              this.showsFirstOption = false;

              const { firstChoiceClipping } = this.$refs;
              const currentHeight = firstChoiceClipping.clientHeight;

              TweenMax.set(firstChoiceClipping, {
                height: currentHeight,
              });
              TweenMax.to(firstChoiceClipping, 0.4, {
                height: 0,
              });
            }
          } else {
            // Desktop and tablet
            firstChoice.animateOnSelection();
            secondChoice.animateOnSelection();
          }
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
  .question-choice-list {
    margin-bottom: 3.5em;

    .first-choice-clipping {
      flex: 1;
    }
    .card-container {
      flex: 1;
      position: relative;
    }
    .card-container:before {
      content: "";
      display: block;
      padding-top: 100%; /* ratio of 1:1*/
    }
  }

  @media #{$breakpoint-mobile} {
    .card-container:first-child {
      margin-bottom: 20px;
    }
    .first-choice-clipping {
      overflow: hidden;
      will-change: height;
    }
  }
  @media #{$breakpoint-tablet} {
    .question-choice-list {
      display: flex;
      justify-content: space-between;

      padding-top: 5.6em;
      margin-bottom: 5.5em;

      .first-choice-clipping {
        margin-right: 20px;
      }
    }
  }
  @media #{$breakpoint-desktop} {
    .question-choice-list {
      display: flex;
      justify-content: space-between;

      padding-top: 4.5em;
      margin-bottom: 6em;

      .first-choice-clipping {
        margin-right: 20px;
      }
    }
  }
</style>
