<template>
  <div class="question-page">
    <div class="lead-text-box">
      <p class="body-big">
        <slot name="lead-text"></slot>
      </p>
    </div>
    <div class="question-box">
      <h2
        class="header-small purple-fg"
      >Question {{questionNumber + 1}} of {{this.$store.state.totalNumberOfQuestions}}</h2>
      <p class="header purple-fg">
        <slot name="question-text"></slot>
      </p>
    </div>

    <QuestionChoiceList
      @useSelectedChoice="revealExplanationAndNextButton"
      v-on="$listeners"
      :correctChoiceIndex="correctChoiceIndex"
    >
      <template v-slot:choice1-image>
        <slot name="choice1-image"></slot>
      </template>
      <template v-slot:choice1-text>
        <slot name="choice1-text"></slot>
      </template>
      <template v-slot:choice2-text>
        <slot name="choice2-text"></slot>
      </template>
      <template v-slot:choice2-image>
        <slot name="choice2-image"></slot>
      </template>
    </QuestionChoiceList>

    <section ref="explanationAndButton" class="explanation-and-button">
      <section class="explanation">
        <!-- PIE CHART -->
        <PieChart
          v-if="chartType === 'pie'"
          ref="chart"
          :dataPoints="chartData"
          :dense="denseChart"
          class="initiallyHidden"
          :fixedImage="isChartFixedImage"
          @heightSet="emitHeight"
        >
          <template v-slot:chart-title>
            <slot name="chart-title"></slot>
          </template>
          <template v-slot:chart-image>
            <slot name="chart-image"></slot>
          </template>
          <template v-slot:chart-source>
            <slot name="chart-source"></slot>
          </template>
        </PieChart>
        <!-- LINE CHART -->
        <LineChart
          v-else-if="chartType === 'line'"
          ref="chart"
          :dense="denseChart"
          :dataPoints="chartData"
          class="initiallyHidden"
          :fixedImage="isChartFixedImage"
          @heightSet="emitHeight"
        >
          <template v-slot:chart-title>
            <slot name="chart-title"></slot>
          </template>
          <template v-slot:chart-image>
            <slot name="chart-image"></slot>
          </template>
          <template v-slot:chart-source>
            <slot name="chart-source"></slot>
          </template>
        </LineChart>
        <!-- BAR CHART -->
        <BarChart
          v-else-if="chartType === 'bar'"
          ref="chart"
          :dense="denseChart"
          :dataPoints="chartData"
          class="initiallyHidden"
          :fixedImage="isChartFixedImage"
        >
          <template v-slot:chart-title>
            <slot name="chart-title"></slot>
          </template>
          <template v-slot:chart-image>
            <slot name="chart-image"></slot>
          </template>
          <template v-slot:chart-source>
            <slot name="chart-source"></slot>
          </template>
        </BarChart>
        <!-- BAR CHART HORIZONTAL -->
        <BarChartHorizontal
          v-else-if="chartType === 'horizontal'"
          ref="chart"
          :dataPoints="chartData"
          class="initiallyHidden"
          :fixedImage="isChartFixedImage"
          @heightSet="emitHeight"
        >
          <template v-slot:chart-title>
            <slot name="chart-title"></slot>
          </template>
          <template v-slot:chart-image>
            <slot name="chart-image"></slot>
          </template>
          <template v-slot:chart-source>
            <slot name="chart-source"></slot>
          </template>
        </BarChartHorizontal>
        <!-- BAR CHART STACKED -->
        <BarChartStacked
          v-else-if="chartType === 'stacked'"
          ref="chart"
          :dense="denseChart"
          :dataPoints="chartData"
          class="initiallyHidden"
          :fixedImage="isChartFixedImage"
        >
          <template v-slot:chart-title>
            <slot name="chart-title"></slot>
          </template>
          <template v-slot:chart-image>
            <slot name="chart-image"></slot>
          </template>
          <template v-slot:chart-source>
            <slot name="chart-source"></slot>
          </template>
        </BarChartstacked>
        <AnswerDetail class="initiallyHidden">
          <slot name="answer-detail"></slot>
        </AnswerDetail>
      </section>
      <div class="next-button initiallyHidden">
        <Button @click="onNextButtonClicked">Next</Button>
      </div>
    </section>
  </div>
</template>

<script>
import { TweenMax } from 'gsap';
import AnswerDetail from '@/components/AnswerDetail.vue';
import Button from '@/components/Button.vue';
import QuestionChoiceList from '@/components/QuestionChoiceList.vue';
import BarChart from '@/components/BarChart.vue';
import BarChartHorizontal from '@/components/BarChartHorizontal.vue';
import BarChartStacked from '@/components/BarChartStacked.vue';
import PieChart from '@/components/PieChart.vue';
import LineChart from '@/components/LineChart.vue';

export default {
  name: 'QuestionPage',
  components: {
    AnswerDetail,
    BarChart,
    BarChartHorizontal,
    BarChartStacked,
    PieChart,
    LineChart,
    Button,
    QuestionChoiceList,
  },
  props: {
    questionNumber: Number,
    correctChoiceIndex: Number,
    chartType: String,
    denseChart: {
      type: Boolean,
      default: false,
    },
    chartData: Array,
    isChartFixedImage: {
      type: Boolean,
      default: false,
    },
  },
  // List of slots
  //  - lead-text
  //  - question-text
  //  - choice1-image
  //  - choice1-text
  //  - choice2-image
  //  - choice2-text
  //  - chart-title
  //  - chart-image
  //  - chart-source
  //  - answer-detail
  methods: {
    revealExplanationAndNextButton() {
      const { explanationAndButton, chart } = this.$refs;

      setTimeout(() => {
        TweenMax.set(explanationAndButton, {
          display: 'initial',
        });
        this.$emit('heightChanged');
      }, 500);

      TweenMax.to(
        explanationAndButton.querySelectorAll('.initiallyHidden'),
        1,
        {
          opacity: 1,
          delay: 0.6,
        },
      );

      setTimeout(() => {
        chart.revealBars();
      }, 1500);
    },
    onNextButtonClicked() {
      if (this.$store.getters.hasMoreQuestions) {
        const route = `question${this.$store.state.currentPage + 2}`;
        this.$router.push(route);
        this.$store.commit('goToNextPage');
      } else {
        this.$router.push('result');
        this.$store.commit('goToNextPage');
      }
    },
    emitHeight() {
      this.$emit('heightChanged');
    },
  },
};
</script>

