  }
  .popup-link:hover {
    font-weight: $semi-bold-weight;
    border-bottom: 2px solid $purple; // Using border-bottom rather than text-decoration for design purpose
  }
}
.explanation {
  margin-bottom: 4em;
}

.explanation-and-button {
  display: none;

  .initiallyHidden {
    opacity: 0;
    will-change: opacity;
  }
}

