import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentPage: 0,
    numberOfCorrectAnswers: 0,
    totalNumberOfQuestions: 5,
  },
  getters: {
    hasMoreQuestions: state => state.currentPage + 1 < state.totalNumberOfQuestions,
  },
  /* eslint-disable no-param-reassign */
  mutations: {
    goToNextPage(state) {
      state.currentPage += 1;
    },
    userDidSelectCorrectAnswer(state) {
      state.numberOfCorrectAnswers += 1;
    },
    resetToInitialState(state) {
      state.currentPage = 0;
      state.numberOfCorrectAnswers = 0;
    },
  },
  /* eslint-enable no-param-reassign */
  actions: {
  },
});
