//
// request / response wrapper for getting scroll offset in parent iframe (for popup positioning).
// requires vertical-positioning-helper.js to be embedded on parent page
//

const requests = {};
let counter = 0;

const listener = (e) => {
  if (e.data.type === 'scroll-pos-response') {
    requests[e.data.uid](e.data);
    delete requests[e.data.uid];
  }
};
window.addEventListener('message', listener, false);

export default function getVerticalPositioningInfo() {
  return new Promise((resolve) => {
    const uid = `r${counter}`;
    counter += 1;

    // attach id'd promise to requests obj, which we'll resolve once a
    // response is received
    requests[uid] = resolve;
    window.parent.postMessage({
      type: 'scroll-pos',
      uid,
    }, '*');
  });
}
