<template>
  <div class="bar-chart" :class="{dense: this.dense}">
    <h3
      :class="{
      'header-small': !this.isChartTitleLarge,
      'text-only-chart-title': this.isChartTitleLarge,
      'fixed-image-title': this.fixedImage,
      'purple-fg': this.isChartTitleLarge}"
    >
      <slot name="chart-title"></slot>
    </h3>
    <figure ref="figure">
      <svg
        v-if="isTextOnlyChart === false"
        class="chart"
      >
      </svg>
      <div class="fixed-image">
        <slot name="chart-image"></slot>
      </div>
    </figure>
    <section class="source">
      <div
        class="info-icon-and-source-label"
        :class="{'touch-device': isTouchDevice}"
        @mouseover="onMouseOver"
        @mouseout="onMouseOut"
        @click="onClick"
        v-click-outside="onClickOutside"
      >
        <div class="info-icon-and-source-label-layout">
          <img class="info-ico" src="../images/icons/iconInfo.svg" alt="Information icon">
          <p class="body-small purple-fg">Source</p>
        </div>
      </div>
      <transition name="fade">
        <div
          class="popover"
          :style="{'top': this.popoverTop + 'px'}"
          v-show="this.isPopoverVisible"
          ref="chartSource"
        >
          <p class="body-small" ref="chartSource">
            <slot name="chart-source"></slot>
          </p>
          <div
            class="arrow-container"
            :style="{'top': this.arrowContainerTop + 'px'}"
          >
            <div class="arrow"></div>
          </div>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>
import * as d3 from 'd3';
import { TweenMax } from 'gsap';
import vClickOutside from 'v-click-outside';
import isTouchDevice from '../util/isTouchDevice';

export default {
  name: 'BarChart',
  directives: {
    clickOutside: vClickOutside.directive,
  },
  data() {
    return {
      isPopoverVisible: false,
      isTouchDevice: isTouchDevice(),
      isAnimationFinished: false,
      popoverTop: 20,
      arrowContainerTop: 20,
      hidePopupTimeout: null,
    };
  },
  props: {
    dataPoints: Array,
    dense: {
      type: Boolean,
      default: false,
    },
    fixedImage: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isTextOnlyChart() {
      return this.dataPoints.length === 0;
    },
    isChartTitleLarge() {
      return this.isTextOnlyChart === true && this.fixedImage === false;
    },
  },
  mounted() {
    if (this.isTextOnlyChart === false) {
      window.addEventListener('resize', this.drawChart);
      this.drawChart();
    }
    window.addEventListener('resize', this.updatePopoverPosition);
  },
  beforeDestroy() {
    if (this.isTextOnlyChart === false) {
      window.removeEventListener('resize', this.drawChart);
    }
    window.removeEventListener('resize', this.updatePopoverPosition);

    clearTimeout(this.hidePopupTimeout);
  },
  methods: {
    revealBars() {
      TweenMax.staggerTo(this.$el.querySelectorAll('.bar-chart rect'), 0.6, {
        scaleY: 1.0,
      }, 0.07, () => {
        this.isAnimationFinished = true;
      });

      TweenMax.staggerTo(this.$el.querySelectorAll('.value-labels text'), 0.3, {
        opacity: 1,
        y: 0,
        delay: 0.4,
      }, 0.07);
    },
    drawChart() {
      // Get current svg area width
      const figureWidth = this.$refs.figure.clientWidth;

      // Define constants for drawing
      const SVG_MAX_WIDTH = 352;
      const SVG_WIDTH = Math.min(figureWidth, SVG_MAX_WIDTH);
      const SVG_HEIGHT = this.dense ? 235 : 220;

      // Function to calculate proportional shrink amount
      const adjustedWidth = (originalWidth) => {
        if (figureWidth < SVG_MAX_WIDTH) {
          return originalWidth - (SVG_MAX_WIDTH - figureWidth) * originalWidth / SVG_MAX_WIDTH;
        }
        return originalWidth;
      };

      // Define the rest of constants
      const LINE_Y = 175;
      const BAR_MAX_WIDTH = this.dense ? 35 : 61;
      const BAR_WIDTH = adjustedWidth(BAR_MAX_WIDTH);
      const BAR_MAX_MARGIN_TO_H_EDGE = 6;
      const BAR_MARGIN_TO_H_EDGE = adjustedWidth(BAR_MAX_MARGIN_TO_H_EDGE);
      const BAR_AREA_WIDTH = SVG_WIDTH - 2 * BAR_MARGIN_TO_H_EDGE;
      const BAR_MAX_HEIGHT = 148;
      const BAR_MARGIN_TO_TOP_EDGE = 27;
      const barsGroupTranslate = `translate(${BAR_MARGIN_TO_H_EDGE},${BAR_MARGIN_TO_TOP_EDGE})`;
      const LABEL_Y = 166;
      const VALUE_LABEL_MARGIN = 5;

      const x = d3.scaleLinear()
        .domain([0, this.dataPoints.length - 1])
        .range([0, BAR_AREA_WIDTH - BAR_WIDTH]);
      const y = d3.scaleLinear()
        .domain([0, d3.max(this.dataPoints, d => d.value)])
        .range([BAR_MAX_HEIGHT, 0]);

      // Grab the reference for svg
      const svg = d3.select(this.$el)
        .select('svg.chart')
        .attr('width', SVG_WIDTH)
        .attr('height', SVG_HEIGHT);

      // Clear out all the previous elements
      svg.selectAll('g')
        .remove();

      // Draw bar
      const highlightedClass = d => (d.highlighted ? 'highlighted' : '');
      svg.append('g')
        .attr('transform', barsGroupTranslate)
        .selectAll('rect')
        .data(this.dataPoints)
        .enter()
        .append('rect')
        .attr('x', (d, i) => x(i))
        .attr('width', BAR_WIDTH)
        .attr('y', d => y(d.value))
        .attr('height', d => BAR_MAX_HEIGHT - y(d.value))
        .attr('class', highlightedClass)
        .attr('class', 'purple');

      if (!this.isAnimationFinished) {
        // Initially set scaleY to 0 for animation
        TweenMax.set(this.$el.querySelectorAll('.bar-chart rect'), {
          scaleY: 0,
          svgOrigin: `0 ${LINE_Y - BAR_MARGIN_TO_TOP_EDGE}`,
        });
      }

      // Draw value label
      svg.append('g')
        .attr('class', 'value-labels')
        .attr('transform', barsGroupTranslate)
        .selectAll('text')
        .data(this.dataPoints)
        .enter()
        .append('text')
        .attr('x', (d, i) => x(i) + BAR_WIDTH / 2)
        .attr('y', d => y(d.value) - VALUE_LABEL_MARGIN)
        .text(d => `${d.value}${d.unit}`);

      if (!this.isAnimationFinished) {
        TweenMax.set(this.$el.querySelectorAll('.value-labels text'), {
          opacity: 0,
          y: 10,
        });
      }

      // Draw title label
      const drawTitleLabel = (labels, additionalClass = '') => {
        svg.append('g')
          .attr('class', 'title-labels')
          .attr('transform', barsGroupTranslate)
          .selectAll('text')
          .data(labels)
          .enter()
          .append('text')
          .attr('class', additionalClass)
          .attr('x', (d, i) => x(i) + BAR_WIDTH / 2)
          .attr('y', LABEL_Y)
          .text(d => d);
      };
      drawTitleLabel(this.dataPoints.map(d => d.label1));
      drawTitleLabel(this.dataPoints.map(d => d.label2), 'second-line');
      drawTitleLabel(this.dataPoints.map(d => d.label3), 'third-line');

      // Draw horizontal line
      svg.append('g')
        .append('line')
        .attr('x1', 0)
        .attr('y1', LINE_Y)
        .attr('x2', SVG_WIDTH)
        .attr('y2', LINE_Y);
    },
    onMouseOver() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.showSourcePopover();
      }
    },
    onMouseOut() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.hidePopupTimeout = setTimeout(this.hideSourcePopover, 1500);
      }
    },
    onClick() {
      if (isTouchDevice()) {
        this.showSourcePopover();
      }
    },
    onClickOutside() {
      if (isTouchDevice()) {
        this.hideSourcePopover();
      }
    },
    showSourcePopover() {
      this.isPopoverVisible = true;
      this.$nextTick(() => {
        this.updatePopoverPosition();
      });
    },
    updatePopoverPosition() {
      const { chartSource } = this.$refs;
      const height = chartSource.clientHeight;
      this.popoverTop = -height - 5;
      this.arrowContainerTop = height - 12;
    },
    hideSourcePopover() {
      if (this.isPopoverVisible) {
        this.isPopoverVisible = false;
      }
    },
  },
};
</script>

