
<template>
  <div class="bar-chart" :class="{dense: this.dense}">
    <h3
      :class="{
      'header-small': !this.isChartTitleLarge,
      'text-only-chart-title': this.isChartTitleLarge,
      'fixed-image-title': this.fixedImage,
      'purple-fg': this.isChartTitleLarge}"
    >
      <slot name="chart-title"></slot>
    </h3>
    <figure ref="figure">
      <svg 
        v-if="isTextOnlyChart === false" 
        class="chart" 
        :class="{'touch-device': isTouchDevice}"
        @click="onChartClick"
        v-click-outside="onChartClickOutside"
      />
      <div class="fixed-image">
        <slot name="chart-image"></slot>
      </div>
    </figure>
    <section class="source">
      <div
        class="info-icon-and-source-label"
        :class="{'touch-device': isTouchDevice}"
        @mouseover="onMouseOver"
        @mouseout="onMouseOut"
        @click="onClick"
        v-click-outside="onClickOutside"
      >
        <div class="info-icon-and-source-label-layout">
          <img class="info-ico" src="../images/icons/iconInfo.svg" alt="Information icon" />
          <p class="body-small purple-fg">Source</p>
        </div>
      </div>
      <transition name="fade">
        <div
          class="popover"
          :style="{'top': this.popoverTop + 'px'}"
          v-show="this.isPopoverVisible"
          ref="chartSource"
        >
          <p class="body-small" ref="chartSource">
            <slot name="chart-source"></slot>
          </p>
          <div class="arrow-container" :style="{'top': this.arrowContainerTop + 'px'}">
            <div class="arrow"></div>
          </div>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>

import * as d3 from 'd3';
import { TweenMax } from 'gsap';
import vClickOutside from 'v-click-outside';
import isTouchDevice from '../util/isTouchDevice';

export default {
  name: 'BarChartStacked',
  directives: {
    clickOutside: vClickOutside.directive,
  },
  data() {
    return {
      isPopoverVisible: false,
      isTouchDevice: isTouchDevice(),
      isAnimationFinished: false,
      popoverTop: 20,
      arrowContainerTop: 20,
      hidePopupTimeout: null,
    };
  },
  props: {
    dataPoints: Array,
    dense: {
      type: Boolean,
      default: false,
    },
    fixedImage: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isTextOnlyChart() {
      return this.dataPoints.length === 0;
    },
    isChartTitleLarge() {
      return this.isTextOnlyChart === true && this.fixedImage === false;
    },
  },
  mounted() {
    if (this.isTextOnlyChart === false) {
      window.addEventListener('resize', this.drawChart);
      this.drawChart();
    }
    window.addEventListener('resize', this.updatePopoverPosition);
  },
  beforeDestroy() {
    if (this.isTextOnlyChart === false) {
      window.removeEventListener('resize', this.drawChart);
    }
    window.removeEventListener('resize', this.updatePopoverPosition);

    clearTimeout(this.hidePopupTimeout);
  },
  methods: {
    revealBars() {
      TweenMax.staggerTo(
        this.$el.querySelectorAll('.bar-chart rect'),
        0.3,
        {
          scaleX: 1.0,
        },
        0.07,
        () => {
          this.isAnimationFinished = true;
        },
      );

      TweenMax.staggerTo(
        this.$el.querySelectorAll('.value-labels-horizontal text'),
        0.3,
        {
          opacity: 1,
          x: 0,
          delay: 0.3,
        },
        0.07,
      );
    },
    drawChart() {
      /* ------- DEFENITIONS AND CONSTANTS ------- */

      // Colors
      const color = d3.scaleOrdinal().range(['#582C83', '#A50151', '#0293B2', '#FBAF17', '#B8BF0E', '#AF99C3', '#D280A7', '#80C8D8']);

      // Get current svg area width
      const numBars = this.dataPoints[0].data.length;

      const BAR_HEIGHT = numBars > 3 ? 45 - (5 * (numBars - 4)) : 45;

      // Define constants for drawing
      const figureWidth = this.$refs.figure.clientWidth;
      const SVG_MAX_WIDTH = 352;
      const SVG_WIDTH = Math.min(figureWidth, SVG_MAX_WIDTH);

      // Area of the data charts
      const BAR_AREA_HEIGHT = (BAR_HEIGHT * 2) * numBars;
      const BAR_AREA_MARGIN_TOP = 35;
      const barsGroupTranslate = `translate(0, ${BAR_AREA_MARGIN_TOP})`;

      // Total area of the SVG: chart + labels
      const SVG_HEIGHT = BAR_AREA_HEIGHT + BAR_AREA_MARGIN_TOP * 2 + 150;

      const opacityHover = 1;
      const otherOpacityOnHover = 0.3;

      // Converters from data to pixel locations
      const x = d3
        .scaleLinear()
        .domain([0, d3.max(this.dataPoints[0].data, d => d3.sum(d.values))])
        .range([0, SVG_WIDTH]);
      const y = d3
        .scaleLinear()
        .domain([0, numBars - 1])
        .range([0, BAR_AREA_HEIGHT]);

      // Grab the reference for svg
      const svg = d3
        .select(this.$el)
        .select('svg.chart')
        .attr('height', SVG_HEIGHT)
        .attr('width', SVG_WIDTH);

      // Clear out all the previous elements
      svg.selectAll('g').remove();


      /* ------- BARS ------- */

      // Helper to determine
      function sumBars(values, idx) {
        let sum = 0;
        for (let z = 0; z < idx; z += 1) {
          sum += x(values[z]);
        }
        return sum;
      }
      

      for (let v = 0; v < this.dataPoints[0].categories.length; v += 1) {
        // Draw bar
        svg
          .append('g')
          .attr('transform', barsGroupTranslate)
          .selectAll('rect')
          .data(this.dataPoints[0].data)
          .enter()
          .append('rect')
          .attr('fill', color(v))
          .attr("class", (d, i) => `category-${v}`)
          .attr('x', d => sumBars(d.values, v))
          .attr('y', (d, i) => y(i))
          .attr('width', d => x(d.values[v]))
          .attr('height', BAR_HEIGHT)
          .on("mouseover", (d, i) => {
            d3.selectAll(".chart>g>rect,.key>g").style("opacity", otherOpacityOnHover);
            d3.selectAll(`.category-${v}`).style("opacity", opacityHover);
          })
          .on("mouseout", () => {
            d3.selectAll(".chart>g>rect,.key>g").style("opacity", 1);
          });

        // Draw value label
        svg
          .append('g')
          .attr('class', 'value-labels-horizontal')
          .attr('transform', barsGroupTranslate)
          .selectAll('text')
          .data(this.dataPoints[0].data)
          .enter()
          .append('text')
          .attr('fill', '#FFFFFF')
          .attr("class", (d, i) => `category-${v}`)
          .attr('y', (d, i) => y(i) + BAR_HEIGHT / 1.5)
          .attr('x', d => sumBars(d.values, v) + (x(d.values[v] / 3)))
          .text(d => `${d.values[v] > 10 ? d.values[v] + `%` : d.values[v]}`)
          .on("mouseover", (d, i) => {
            d3.selectAll(".chart>g>rect,.key>g").style("opacity", otherOpacityOnHover);
            d3.selectAll(`.category-${v}`).style("opacity", opacityHover);
          })
          .on("mouseout", () => {
            d3.selectAll(".chart>g>rect,.key>g").style("opacity", 1);
          });
      }

      // Initially set scaleY to 0 for animation
      if (!this.isAnimationFinished) {
        TweenMax.set(this.$el.querySelectorAll('.bar-chart rect'), {
          scaleX: 0,
        });
      }

      // Animation initial state if first time
      if (!this.isAnimationFinished) {
        TweenMax.set(
          this.$el.querySelectorAll('.value-labels-horizontal text'),
          {
            opacity: 0,
            x: -10,
          },
        );
      }


      /* ------- BAR LABEL ------- */

      // Draw bar label
      const drawTitleLabel = (labels, additionalClass = '') => {
        svg
          .append('g')
          .attr('class', 'title-labels-horizontal')
          .attr('transform', barsGroupTranslate)
          .selectAll('text')
          .data(labels)
          .enter()
          .append('text')
          .attr('class', additionalClass)
          .attr('y', (d, i) => y(i) - 7)
          .attr('x', 0)
          .text(d => d);
      };
      drawTitleLabel(this.dataPoints[0].data.map(d => d.label));


      /* ------- KEY -------*/

      svg.append('g')
      .attr('class', 'key')

      const labels = svg.select('.key').selectAll('text').data(this.dataPoints[0].categories);

      const label = labels.enter().append('g').attr('class', (d,i) => `category-${i}`);

      // Key label
      label
        .append('text')
        .attr('x', 20)
        .attr('y', (d, i) => BAR_AREA_HEIGHT + (BAR_AREA_MARGIN_TOP * 2.75) + i * 22)
        .text(d => `${d}`);

      // Color block
      label
        .append('rect')
        .attr('x', 0)
        .attr('y', (d, i) => BAR_AREA_HEIGHT + (BAR_AREA_MARGIN_TOP * 2.75) + (i * 22) - 10)
        .attr('width', 12)
        .attr('height', 12)
        .style('fill', (d, i) => `${color(i)}`);

      this.$emit('heightSet');
    },
    onMouseOver() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.showSourcePopover();
      }
    },
    onMouseOut() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.hidePopupTimeout = setTimeout(this.hideSourcePopover, 1500);
      }
    },
    onClick() {
      if (isTouchDevice()) {
        this.showSourcePopover();
      }
    },
    onClickOutside() {
      if (isTouchDevice()) {
        this.hideSourcePopover();
      }
    },
    onChartClick() {},  
    onChartClickOutside() {
      if (isTouchDevice()) {
         d3.selectAll(".chart>g>rect,.key>g").style("opacity", 1);
      }
    },
    showSourcePopover() {
      this.isPopoverVisible = true;
      this.$nextTick(() => {
        this.updatePopoverPosition();
      });
    },
    updatePopoverPosition() {
      const { chartSource } = this.$refs;
      const height = chartSource.clientHeight;
      this.popoverTop = -height - 5;
      this.arrowContainerTop = height - 12;
    },
    hideSourcePopover() {
      if (this.isPopoverVisible) {
        this.isPopoverVisible = false;
      }
    },
  },
};
</script>

<style lang="scss"  >
.bar-chart {
  h3 {
    padding-bottom: 4px;
  }
  h3.text-only-chart-title {
    padding-bottom: 15px;
  }
  .fixed-image {
    img {
