    font-weight: $medium-weight;
    font-size: 1.4em;
  }
  .value-labels-horizontal {
    text-anchor: start;
    font-weight: $semi-bold-weight;
    font-size: 1.4em;


    text {
      pointer-events: none;
    }
  }

  .key {
    text-anchor: start;
    font-weight: $medium-weight;
    font-size: 1.5em;

    text {
      will-change: transform;
    }
  }
  .source {
    position: relative;
    padding-top: 9px;
  }
  .info-icon-and-source-label {
    position: relative;
    z-index: 200;
    display: inline-block;
    cursor: pointer;
    img,
    p {
      display: inline-block;
    }
    p {
      font-weight: $semi-bold-weight;
      margin-left: 5px;
      line-height: 1.1;
    }
  }
  // Expand the hit area for better usability
  // Ref: http://front-back.com/expand-clickable-areas-for-a-better-touch-experience
  .info-icon-and-source-label:after {
    content: "";
    position: absolute;
    top: -12px;
    bottom: -12px;
    left: -3px;
    right: -3px;
  }

  .info-icon-and-source-label-layout {
    display: flex;
    align-items: center;
  }

  .popover {
    position: absolute;
    z-index: 100;
    left: -31px;
    //top: -70px;
    background-color: $white;
    border-radius: 5px;
    box-shadow: 4px -1px 14px 0px rgba(0, 0, 0, 0.15);

    p {
      padding: 11px 22px 12px 17px;
    }
  }

  .arrow-container {
    position: absolute;
    //top: 53px;
    left: 31px;
    clip: rect(12px, 45px, 36px, -10px);
    overflow: visible;
  }

  .arrow {
    width: 20px;
    height: 20px;
    background-color: white;
    transform: scale(1, 0.9) rotate(45deg);
    box-shadow: 4px -1px 14px 0px rgba(0, 0, 0, 0.1);
  }
}

