<template>
  <div class="line-chart" :class="{ dense: this.dense }">
    <h3
      :class="{
        'header-small': !this.isChartTitleLarge,
        'text-only-chart-title': this.isChartTitleLarge,
        'fixed-image-title': this.fixedImage,
        'purple-fg': this.isChartTitleLarge
      }"
    >
      <slot name="chart-title"></slot>
    </h3>
    <figure ref="figure">
      <svg v-if="isTextOnlyChart === false" class="chart" />
      <div class="fixed-image">
        <slot name="chart-image"></slot>
      </div>
    </figure>
    <section class="source">
      <div
        class="info-icon-and-source-label"
        :class="{ 'touch-device': isTouchDevice }"
        @mouseover="onMouseOver"
        @mouseout="onMouseOut"
        @click="onClick"
        v-click-outside="onClickOutside"
      >
        <div class="info-icon-and-source-label-layout">
          <img class="info-ico" src="../images/icons/iconInfo.svg" alt="Information icon" />
          <p class="body-small purple-fg">Source</p>
        </div>
      </div>
      <transition name="fade">
        <div
          class="popover"
          :style="{ top: this.popoverTop + 'px' }"
          v-show="this.isPopoverVisible"
          ref="chartSource"
        >
          <p class="body-small" ref="chartSource">
            <slot name="chart-source"></slot>
          </p>
          <div class="arrow-container" :style="{ top: this.arrowContainerTop + 'px' }">
            <div class="arrow"></div>
          </div>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>
import * as d3 from "d3";
import { TweenMax } from "gsap";
import vClickOutside from "v-click-outside";
import isTouchDevice from "../util/isTouchDevice";

export default {
  name: "LineChart",
  directives: {
    clickOutside: vClickOutside.directive
  },
  data() {
    return {
      isPopoverVisible: false,
      isTouchDevice: isTouchDevice(),
      isAnimationFinished: false,
      popoverTop: 20,
      arrowContainerTop: 20,
      hidePopupTimeout: null
    };
  },
  props: {
    dataPoints: Array,
    dense: {
      type: Boolean,
      default: false
    },
    fixedImage: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    isTextOnlyChart() {
      return this.dataPoints.length === 0;
    },
    isChartTitleLarge() {
      return this.isTextOnlyChart === true && this.fixedImage === false;
    }
  },
  mounted() {
    if (this.isTextOnlyChart === false) {
      window.addEventListener("resize", this.drawChart);
      this.drawChart();
    }
    window.addEventListener("resize", this.updatePopoverPosition);
  },
  beforeDestroy() {
    if (this.isTextOnlyChart === false) {
      window.removeEventListener("resize", this.drawChart);
    }
    window.removeEventListener("resize", this.updatePopoverPosition);

    clearTimeout(this.hidePopupTimeout);
  },
  methods: {
    revealBars() {
      TweenMax.staggerTo(
        this.$el.querySelectorAll(".circle-cap.end"),
        0.3,
        { opacity: 1 },
        0.3,
        () => {
          this.isAnimationFinished = true;
        }
      );
    },
    drawChart() {
      /* ------- CONSTANTS & DEFINITIONS -------*/

      // Define pie color pallette
      const color = [
        "#582C83",
        "#A50151",
        "#0293B2",
        "#FBAF17",
        "#B8BF0E",
        "#AF99C3",
        "#D280A7",
        "#80C8D8"
      ];

      // Margin
      const margin = {
        top: 15,
        right: 50,
        bottom: 25,
        left: 25
      };

      // More than one line?
      const multiline = this.dataPoints.length > 1;

      // Get current svg area width
      const figureWidth = this.$refs.figure.clientWidth;

      // Define constants for drawing
      const SVG_MAX_WIDTH = 352;
      const SVG_WIDTH =
        figureWidth === 0
          ? SVG_MAX_WIDTH
          : Math.min(figureWidth, SVG_MAX_WIDTH);
      const SVG_HEIGHT = 250 + 25 * (this.dataPoints.length + 1);

      const width = SVG_WIDTH - margin.right;
      const height = 250;

      const svg = d3
        .select(this.$el)
        .select("svg.chart")
        .attr("width", width)
        .attr("height", SVG_HEIGHT + margin.top + margin.bottom)
        .attr("transform", `translate(0,${margin.top})`);

      // Clear out all the previous elements
      svg.selectAll("g").remove();
      svg.selectAll("path").remove();
      svg.selectAll("circle").remove();
      svg.selectAll("line").remove();

      /* ------- BOUNDARY MATH -------*/

      // Min and max of range
      let ymin;
      let ymax;
      let xmin;
      let xmax;

      // If more than one line
      if (multiline) {
        ymin = d3.min(this.dataPoints, d => d3.min(d.data, a => a.y));
        ymax = d3.max(this.dataPoints, d => d3.max(d.data, a => a.y));
        xmin = d3.min(this.dataPoints, d => d3.min(d.data, a => a.x));
        xmax = d3.max(this.dataPoints, d => d3.max(d.data, a => a.x));
      } else {
        ymin = d3.min(this.dataPoints, d => d.y);
        ymax = d3.max(this.dataPoints, d => d.y);
        xmin = d3.min(this.dataPoints, d => d.x);
        xmax = d3.max(this.dataPoints, d => d.x);
      }

      // X scale
      const xScale = d3
        .scaleLinear()
        .domain([xmin, xmax]) // input
        .range([0, width]); // output

      // Y scale
      // Padded with 10% of the total range
      const yScale = d3
        .scaleLinear()
        .domain([
          ymin - (ymax + Math.abs(ymin)) / 10,
          ymax + (ymax + Math.abs(ymin)) / 10
        ]) // input
        .range([height, 0]); // output

      /* ------- AXIS -------*/

      // X axis
      svg
        .append("g")
        .attr("class", "x-axis")
        .attr("transform", `translate(0,${height})`)
        .call(d3.axisBottom(xScale).ticks(5)); // Create an axis component with d3.axisBottom

      // Y Axis
      svg
        .append("g")
        .attr("class", "y-axis")
        .call(d3.axisLeft(yScale).ticks(10)); // Create an axis component with d3.axisLeft

      function makeXGridlines() {
        return d3.axisLeft(yScale).ticks(10);
      }
      // Grid lines
      svg
        .append("g")
        .attr("class", "grid")
        .style("stroke-dasharray", "3,3")
        .call(
          makeXGridlines()
            .tickSize(-width)
            .tickFormat("")
        );

      if (ymin < 0) {
        console.log(xmax);
        svg
          .append("line")
          .style("stroke", "#c4c4c4")
          .attr("x1", xScale(0))
          .attr("y1", yScale(0))
          .attr("x2", xScale(xmax))
          .attr("y2", yScale(0));
      }

      /* ------- LINES -------*/

      for (let i = 0; i < this.dataPoints.length; i += 1) {
        // D3 line
        const line = d3
          .line()
          .x(d => xScale(d.x)) // Set the x values for the line generator
          .y(d => yScale(d.y)); // Set the y values for the line generator

        // An array of objects of length N. Each object has key -> value pair, the key being "y" and the value is a random number
        const dataset = this.dataPoints[i].data.map(d => ({
          y: d.y,
          x: d.x
        }));

        // Bind data, call line generator
        svg
          .append("path")
          .datum(dataset) // Binds data to the line
          .attr("class", "line")
          .attr("id", `line-${i}`)
          .attr("stroke", color[i])
          .attr("stroke-width", 3)
          .attr("fill", "none")
          .attr("d", line); // Calls the line generator

        // Circle caps at start
        svg
          .append("circle")
          .attr("class", "circle-cap")
          .attr("r", 6)
          .attr("fill", color[i])
          .attr("stroke", "#F2F2F2")
          .attr("stroke-width", "2")
          .attr("cx", xScale(this.dataPoints[i].data[0].x))
          .attr("cy", yScale(this.dataPoints[i].data[0].y));

        // Circle caps at end
        svg
          .append("circle")
          .attr("class", "circle-cap end")
          .attr("r", 6)
          .attr("fill", color[i])
          .attr("stroke", "#F2F2F2")
          .attr("stroke-width", "2")
          .attr(
            "cx",
            xScale(
              this.dataPoints[i].data[this.dataPoints[i].data.length - 1].x
            )
          )
          .attr(
            "cy",
            yScale(
              this.dataPoints[i].data[this.dataPoints[i].data.length - 1].y
            )
          );
      }

      /* ------- TEXT LABELS -------*/

      svg.append("g").attr("class", "key");

      const labels = svg
        .select(".key")
        .selectAll("text")
        .data(this.dataPoints, d => `${d.label}`);

      const label = labels.enter().append("g");

      // Label
      label
        .append("text")
        .attr("x", 30)
        .attr("y", (d, i) => 300 + i * 20)
        .text(d => `${d.label}`);

      // Color block
      label
        .append("rect")
        .attr("x", 0)
        .attr("y", (d, i) => 294 + i * 20)
        .attr("width", 20)
        .attr("height", 4)
        .style("fill", (d, i) => `${color[i]}`);

      /* ------- ANIMATION HELPER ------- */

      // Initial animation states if drawing for the first time
      if (!this.isAnimationFinished) {
        d3.selectAll(".line")
          .attr("stroke-dasharray", `${500} ${500}`)
          .attr("stroke-dashoffset", 500)
          .transition()
          .delay((d, i) => i * 300)
          .duration(2000)
          .attr("stroke-dashoffset", 0);

        TweenMax.set(this.$el.querySelectorAll(".circle-cap.end"), {
          opacity: 0
        });
      }
    },
    onMouseOver() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.showSourcePopover();
      }
    },
    onMouseOut() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.hidePopupTimeout = setTimeout(this.hideSourcePopover, 1500);
      }
    },
    onClick() {
      if (isTouchDevice()) {
        this.showSourcePopover();
      }
    },
    onClickOutside() {
      if (isTouchDevice()) {
        this.hideSourcePopover();
      }
    },
    showSourcePopover() {
      this.isPopoverVisible = true;
      this.$nextTick(() => {
        this.updatePopoverPosition();
      });
    },
    updatePopoverPosition() {
      const { chartSource } = this.$refs;
      const height = chartSource.clientHeight;
      this.popoverTop = -height - 5;
      this.arrowContainerTop = height - 12;
    },
    hideSourcePopover() {
      if (this.isPopoverVisible) {
        this.isPopoverVisible = false;
      }
    }
  }
};
</script>

