<template>
  <div class="pie-chart">
    <h3
      :class="{
        'header-small': !this.isChartTitleLarge,
        'text-only-chart-title': this.isChartTitleLarge,
        'fixed-image-title': this.fixedImage,
        'purple-fg': this.isChartTitleLarge
      }"
    >
      <slot name="chart-title"></slot>
    </h3>
    <figure ref="figure">
      <svg 
        v-if="isTextOnlyChart === false"
        class="chart"
        id="svg-chart"
        :class="{'touch-device': isTouchDevice}"
        @click="onPieClick"
        v-click-outside="onPieClickOutside"
      />
      <div class="fixed-image">
        <slot name="chart-image"></slot>
      </div>
    </figure>
   <section class="source">
      <div
        class="info-icon-and-source-label"
        :class="{'touch-device': isTouchDevice}"
        @mouseover="onMouseOver"
        @mouseout="onMouseOut"
        @click="onClick"
        v-click-outside="onClickOutside"
      >
        <div class="info-icon-and-source-label-layout">
          <img class="info-ico" src="../images/icons/iconInfo.svg" alt="Information icon" />
          <p class="body-small purple-fg">Source</p>
        </div>
      </div>
      <transition name="fade">
        <div
          class="popover"
          :style="{'top': this.popoverTop + 'px'}"
          v-show="this.isPopoverVisible"
          ref="chartSource"
        >
          <p class="body-small" ref="chartSource">
            <slot name="chart-source"></slot>
          </p>
          <div class="arrow-container" :style="{'top': this.arrowContainerTop + 'px'}">
            <div class="arrow"></div>
          </div>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>
import * as d3 from "d3";
import { TweenMax } from "gsap";
import vClickOutside from "v-click-outside";
import isTouchDevice from "../util/isTouchDevice";

export default {
  name: "PieChart",
  directives: {
    clickOutside: vClickOutside.directive
  },
  data() {
    return {
      isPopoverVisible: false,
      isTouchDevice: isTouchDevice(),
      isAnimationFinished: false,
      popoverTop: 20,
      arrowContainerTop: 20,
      hidePopupTimeout: null
    };
  },
  props: {
    dataPoints: Array,
    dense: {
      type: Boolean,
      default: false
    },
    fixedImage: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    isTextOnlyChart() {
      return this.dataPoints.length === 0;
    },
    isChartTitleLarge() {
      return this.isTextOnlyChart === true && this.fixedImage === false;
    }
  },
  mounted() {
    if (this.isTextOnlyChart === false) {
      window.addEventListener("resize", this.drawChart);
      this.drawChart();
    }
    window.addEventListener("resize", this.updatePopoverPosition);
  },
  beforeDestroy() {
    if (this.isTextOnlyChart === false) {
      window.removeEventListener("resize", this.drawChart);
    }
    window.removeEventListener("resize", this.updatePopoverPosition);

    clearTimeout(this.hidePopupTimeout);
  },
  methods: {
    revealBars() {
      TweenMax.staggerTo(
        this.$el.querySelectorAll(".pie > g"),
        0.5,
        {
          opacity: 1
        },
        0.1,
        () => {
          this.isAnimationFinished = true;
        }
      );

      TweenMax.staggerTo(
        this.$el.querySelectorAll(".label"),
        0.5,
        {
          opacity: 1,
          y: 0
        },
        0.1
      );
    },
    drawChart() {
      // Get current svg area width
      const figureWidth = this.$refs.figure.clientWidth;

      // Define constants for drawing
      const SVG_MAX_WIDTH = 350;
      
      let SVG_WIDTH;

      // Bug fix for when figurewidth is calculated yet
      if (figureWidth === 0) {
        SVG_WIDTH = SVG_MAX_WIDTH;
      } else {
        SVG_WIDTH = Math.min(figureWidth, SVG_MAX_WIDTH);
      }

      const SVG_HEIGHT = SVG_WIDTH + 55 * (this.dataPoints.length + 1);

      const width = SVG_WIDTH;
      const height = SVG_HEIGHT;

      const padding = 10;
      const opacityHover = 1;
      const otherOpacityOnHover = 0.3;

      const radius = Math.min(width - padding, height - padding) / 2;

      // Define pie color pallette
      const color = d3
        .scaleOrdinal()
        .range([
          "#582C83",
          "#A50151",
          "#0293B2",
          "#FBAF17",
          "#B8BF0E",
          "#AF99C3",
          "#D280A7",
          "#80C8D8"
        ]);
      const svg = d3
        .select(this.$el)
        .select("svg.chart")
        .attr("width", width)
        .attr("height", height);

      // Clear out all the previous elements
      svg.selectAll("g").remove();
      svg.selectAll("svg").remove();

      /* ------- PIE -------*/

      const g = svg
        .append("g")
        .attr("class", "pie")
        .attr("transform", `translate(${width / 2},${(width / 2) + 25})`);

      const arc = d3
        .arc()
        .innerRadius(0)
        .outerRadius(radius);

      const pie = d3
        .pie()
        .value(d => d.value)
        .sort(null);

      const self = this;

      const tooltipGroup = svg
        .append("svg")
        .attr("x", 0)
        .attr("y", 0)
        .style("opacity", 0)
        .attr("class", "tooltip");

      const tooltipBack = tooltipGroup
        .append("rect")
        .attr("x", 0)
        .attr("y", 0)
        .attr("rx", 5)
        .attr("ry", 5)
        .attr("width", 100)
        .attr("height", 50)
        .attr("fill", "#FFFFFF");

      // Tooltip text
      const tooltipLabel = tooltipGroup
        .append("text")
        .attr("x", 12)
        .attr("y", 20)
        .text("Label");

      // Tooltip text
      const tooltipValue = tooltipGroup
        .append("text")
        .attr("x", 12)
        .attr("y", 38)
        .text("Value")
        .attr("class", "tooltip");

      // Bounding box of the svg chart
      // Uses the position of the chart relative to the page
      // to determine the correct offset for the toolip
      var chartRect = document
        .getElementById("svg-chart")
        .getBoundingClientRect();

      var documentClick = false;

      // Pie slices
      g.selectAll("path")
        .data(pie(this.dataPoints))
        .enter()
        .append("g")
        .append("path")
        .attr("id", (d, i) => `pie-${i}`)
        .attr("d", arc)
        .attr("fill", (d, i) => color(i))
        .style("opacity", 1)
        .on("mouseover", (d, i) => {
          if (self.isAnimationFinished) {
            // Set label and value text
            tooltipLabel.text(d.data.label);
            tooltipValue.text(d.data.value + "%");

            // Set background to be size of text
            tooltipBack.attr("width", tooltipLabel.node().getBBox().width + 23);

            // Fade in tooltip
            tooltipGroup
              .transition()
              .duration(250)
              .style("opacity", 1);

            d3.selectAll(".pie>g>path").style("opacity", otherOpacityOnHover);
            d3.selectAll(".value-labels>.label").style(
              "opacity",
              otherOpacityOnHover
            );
            d3.select(`#pie-${i}`).style("opacity", opacityHover);
            d3.select(`#label-${i}`).style("opacity", opacityHover);
          }
        })
        .on("mousemove", (d, i) => {
          if (self.isAnimationFinished) {
            // Move tooltip to follow mouse
            tooltipGroup
              .attr("x", event.pageX - chartRect.x + 30 + "px")
              .attr("y", event.pageY - chartRect.y - 10 + "px");
          }
        })
        .on("mouseout", () => {
          if (self.isAnimationFinished) {
            // Hide tooltip
            tooltipGroup
              .transition()
              .duration(250)
              .style("opacity", 0);
            d3.selectAll(".pie>g>path").style("opacity", 1);
            d3.selectAll(".label").style("opacity", 1);
          }
        });


      /* ------- TEXT LABELS -------*/

      svg.append("svg").attr("class", "value-labels");

      const labels = svg
        .select(".value-labels")
        .selectAll("text")
        .data(pie(this.dataPoints), d => `${d.data.label}`)
        .attr("width", 300);

      const label = labels.enter().append("g");

      // Setting attributes
      label.attr("id", (d, i) => `label-${i}`).attr("class", "label");

      // Title
      label
        .append("text")
        .attr("x", 20)
        .attr("y", (d, i) => width + 63 + i * 50)
        .text(d => `${d.data.label}`);

      // Value
      label
        .append("text")
        .attr("class", "value")
        .attr("x", 20)
        .attr("y", (d, i) => width + 83 + i * 50)
        .text(d => `${d.data.value}%`);

      // Color block
      label
        .append("rect")
        .attr("x", 0)
        .attr("y", (d, i) => width + 50 + i * 50)
        .attr("width", 10)
        .attr("height", 37)
        .style("fill", (d, i) => `${color(i)}`);

      // Initial animation states if drawing for the first time
      if (!this.isAnimationFinished) {
        TweenMax.set(this.$el.querySelectorAll(".label"), {
          opacity: 0
        });
        TweenMax.set(this.$el.querySelectorAll(".pie > g"), {
          opacity: 0
        });
      }
    },
    onMouseOver() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.showSourcePopover();
      }
    },
    onMouseOut() {
      if (!isTouchDevice()) {
        clearTimeout(this.hidePopupTimeout);
        this.hidePopupTimeout = setTimeout(this.hideSourcePopover, 1500);
        d3.selectAll(".pie>g>path").style("opacity", 1);
      }
    },
    onClick() {
      if (isTouchDevice()) {
        this.showSourcePopover();
      }
    },
    onClickOutside() {
      if (isTouchDevice()) {
        this.hideSourcePopover();
      }
    }, 
    onPieClick() {},  
    onPieClickOutside() {
      if (isTouchDevice()) {
         d3.selectAll(".pie>g>path").style("opacity", 1);
         d3.selectAll(".label").style("opacity", 1);
      }
    },
    showSourcePopover() {
      
      this.isPopoverVisible = true;
      this.$nextTick(() => {
        this.updatePopoverPosition();
      });
    },
    updatePopoverPosition() {
      const { chartSource } = this.$refs;
      const height = chartSource.clientHeight;
      this.popoverTop = -height - 5;
      this.arrowContainerTop = height - 12;
    },
    hideSourcePopover() {
      if (this.isPopoverVisible) {
        this.isPopoverVisible = false;
      }
    }
  }
};
</script>

<style lang="scss" >
.pie-chart {
  
  h3 {
    padding-bottom: 4px;
  }
  h3.text-only-chart-title {
    padding-bottom: 15px;
  }
  .fixed-image {
    img {
      width: 100%;
      margin: 10px 0 15px;
    }
  }
  figure {
    #svg-chart {
      display: block;
      margin: 0 auto;
      overflow: visible;

      .tooltip {
        text {
          pointer-events: none;
          text-anchor: start;
          font-weight: $medium-weight;
          font-size: 1.5em;
          will-change: transform;
        }
      }

      .value-labels {
        overflow: hidden;
        text-anchor: start;
        font-weight: $medium-weight;
        font-size: 1.75em;

        text {
          will-change: transform;
        }

        .value {
          font-weight: 300;
        }
      }
    }
  }
  .source {
    position: relative;
    padding-top: 9px;
  }
  
  .info-icon-and-source-label {
    position: relative;
    z-index: 200;
    display: inline-block;
    cursor: pointer;
    img,
    p {
      display: inline-block;
    }
    p {
      font-weight: $semi-bold-weight;
      margin-left: 5px;
      line-height: 1.1;
    }
  }
  // Expand the hit area for better usability
  // Ref: http://front-back.com/expand-clickable-areas-for-a-better-touch-experience
  .info-icon-and-source-label:after {
    content: "";
    position: absolute;
    top: -12px;
    bottom: -12px;
    left: -3px;
    right: -3px;
  }

  .info-icon-and-source-label-layout {
    display: flex;
    align-items: center;
  }

  .popover {
    position: absolute;
    z-index: 100;
    left: -31px;
    //top: -70px;
    background-color: $white;
    border-radius: 5px;
    box-shadow: 4px -1px 14px 0px rgba(0, 0, 0, 0.15);

    p {
      padding: 11px 22px 12px 17px;
    }
  }

  .arrow-container {
    position: absolute;
    //top: 53px;
    left: 31px;
    clip: rect(12px, 45px, 36px, -10px);
    overflow: visible;
  }

  .arrow {
    width: 20px;
    height: 20px;
    background-color: white;
    transform: scale(1, 0.9) rotate(45deg);
    box-shadow: 4px -1px 14px 0px rgba(0, 0, 0, 0.1);
  }
}

@media #{$breakpoint-mobile} {
  .tooltip {
    display: none;
  }
}

@media #{$breakpoint-tablet} {
  .pie-chart {
    min-width: 0;
    max-width: 380px;
    flex: 4 4 0;
    padding-right: 20px;

    h3 {
      margin-left: 0.4em;
      padding-right: 2em;
      padding-bottom: 2px;
    }
    h3.text-only-chart-title {
      margin-left: 0;
      padding-right: 0;
    }
    h3.fixed-image-title {
      margin-left: 0;
      padding-right: 0;
    }
  }
}
@media #{$breakpoint-desktop} {
  .pie-chart {
    flex: 1;
    padding-right: 20px;
    margin-right: 20px;

    h3 {
      margin-left: 0.4em;
      padding-right: 1.7em;
      padding-bottom: 2px;
    }
    h3.text-only-chart-title {
      margin-left: 0;
      padding-right: 0;
    }
    h3.fixed-image-title {
      margin-left: 0;
      padding-right: 0;
    }
  }
}
</style>
