<template>
  <div class="result-page">
    <section class="correct-answers">
      <p class="big-number">
        {{animatingScoreForText}}/{{this.$store.state.totalNumberOfQuestions}}</p>
      <h2 class="header-small">Correct answers</h2>
    </section>
    <div ref="twoColumns" class="two-columns">
      <div class="left-column">
        <article class="wrap-ups">
          <h3 class="header purple-fg">
            <span v-if="scoreRank === 'highScore'">
              You know a lot about the state of VBC
            </span>
            <span v-if="scoreRank === 'averageScore'">
              You know a few things about the state of VBC
            </span>
            <span v-if="scoreRank === 'lowScore'">
              Time to learn more about the state of VBC
            </span>
          </h3>
          <p class="body">
            <span v-if="scoreRank === 'highScore'">
              Healthcare is clearly headed toward value-based care (VBC).
              Successful organizations will understand the state of the market
              and how to navigate the still uncertain landscape to their advantage.
            </span>
            <span v-if="scoreRank === 'averageScore'">
              Healthcare is clearly headed toward value-based care (VBC).
              Successful organizations will understand the state of the market
              and how to navigate the still uncertain landscape to their advantage.
            </span>
            <span v-if="scoreRank === 'lowScore'">
              Healthcare is clearly headed toward value-based care (VBC).
              Successful organizations will understand the state of the market
              and how to navigate the still uncertain landscape to their advantage.
            </span>
          </p>
        </article>
        <section class="buttons">
          <div>
            <Button :cta="true" @click="onCTAButtonClicked">
              Read more about value-based care
            </Button>
          </div>
          <div>
            <Button @click="onTakeQuizAgainClicked">
              Take the quiz again
            </Button>
          </div>
        </section>
        <section class="sharing-score">
          <h3 class="header-small purple-fg">Share your score:</h3>
          <ul>
            <li>
              <a :href="twitterShareURL" target="_blank">
                <img src="../images/icons/iconTwitter.svg" alt="Twitter icon">
              </a>
            </li>
            <li>
              <a :href="facebookShareURL" target="_blank">
                <img src="../images/icons/iconFB.svg" alt="Facebook icon">
              </a>
            </li>
            <li>
              <a :href="linkedInShareURL" target="_blank">
                <img src="../images/icons/iconLinkedIn.svg" alt="Facebook icon">
              </a>
            </li>
            <li>
              <a :href="emailShareURL" target="_blank" rel="noopener noreferrer" aria-label="Email">
                <img src="../images/icons/iconEmail.svg" alt="Email icon">
              </a>
            </li>
          </ul>
        </section>
      </div>
      <div class="right-column">
        <article class="resources">
          <h3 class="header-small purple-fg">
            Make VBC as easy as ABC. We can help.
          </h3>
          <ArticleCarousel/>
        </article>
      </div>
    </div>
  </div>
</template>

<script>
import { TweenMax, Linear } from 'gsap';
import ArticleCarousel from '@/components/ArticleCarousel.vue';
import Button from '@/components/Button.vue';

export default {
  name: 'ResultPage',
  components: {
    ArticleCarousel,
    Button,
  },
  data() {
    return {
      url: 'https://www.athenahealth.com/insight/',
      hashtags: ['healthIT', 'valuebasedcare'],
      twitterUser: 'athenahealth',
      twitterDescription: encodeURIComponent("Value-based care is here to stay, but it's not one-size-fits-all. Do you know which trends are taking hold?"),
      animatingScore: 0,
      emailSubject: encodeURIComponent('Quiz: How do patients behave like consumers?'),
    };
  },
  computed: {
    animatingScoreForText() {
      return this.animatingScore.toFixed(0);
    },
    scoreRank() {
      const score = this.$store.state.numberOfCorrectAnswers;
      if (score <= 1) {
        return 'lowScore';
      }
      if (score <= 3) {
        return 'averageScore';
      }
      return 'highScore';
    },
    description() {
      return encodeURIComponent(`Value-based care is here to stay, but it's not one-size-fits-all. Do you know which trends are taking hold? Take the quiz to test your knowledge. I scored ${this.$store.state.numberOfCorrectAnswers}/${this.$store.state.totalNumberOfQuestions}.`);
    },
    emailBody() {
      return `${this.description}%0D%0A%0D%0A${encodeURIComponent(this.url)}`;
    },
    twitterShareURL() {
      return `https://twitter.com/intent/tweet?text=${this.twitterDescription}&url=${this.url}&via=${this.twitterUser}&hashtags=${this.hashtags}`;
    },
    facebookShareURL() {
      return `https://www.facebook.com/sharer/sharer.php?u=${this.url}&quote=${this.description}`;
    },
    linkedInShareURL() {
      return `https://www.linkedin.com/shareArticle?mini=true&url=${this.url}`;
    },
    emailShareURL() {
      return `mailto:?subject=${this.emailSubject}&body=${this.emailBody}`;
    },
  },
  mounted() {
    this.$emit('heightChanged');
    this.animateElements();
  },
  methods: {
    animateElements() {
      const DEFAULT_DELAY = 1.6;
      const countUpDuration = 0.066 * this.$store.state.numberOfCorrectAnswers;
      TweenMax.to(this.$data, countUpDuration, {
        animatingScore: this.$store.state.numberOfCorrectAnswers,
        delay: DEFAULT_DELAY,
        ease: Linear.easeNone,
      });

      const fadeInDelay = DEFAULT_DELAY + countUpDuration / 2;
      const { twoColumns } = this.$refs;
      TweenMax.to(twoColumns, 1.0, {
        opacity: 1,
        delay: fadeInDelay,
      });
    },
    onCTAButtonClicked() {
      window.open('https://www.athenahealth.com/insight/best-practices-getting-started-value-based-care', '_blank');
    },
    onTakeQuizAgainClicked() {
      this.$store.commit('resetToInitialState');
      this.$router.push('/');
    },
  },
};
</script>

